.messageWrapper {
    margin: 25px;
    display: flex;
    align-items: flex-end;
}
.angle {
    width: 20px;
    height: 20px;
    background: radial-gradient(circle at 0 0, rgba(0,0,0,0) 20px,rgba(173,255,47,0.9) 0);
}
.messageWrapper img {
    width: 80px;
    height: 80px;
    margin-right: 10px;
    border-radius: 25px;
}

.message {
    padding: 15px;
    width: 250px;
    position: relative;
    background-color: #adff2f;
    border-radius: 30px 30px 30px 0px;
}

